import React from 'react'
import moment from 'moment';
import Moment from 'react-moment';

import TextField from '@material-ui/core/TextField';

import Layout from '../components/layout'

class IndexPage extends React.Component {
  static displayName = 'TestComponent';

  state = { time: moment().format('YYYY-MM-DD HH:mm:ss') };
  
  render() {

    return <div>
      <Layout>
        <div style={{marginBottom: 30 + 'px'}}>
          <h3>Chọn giờ bay</h3>
          <form noValidate>
            <TextField
              id="time"
              label="Alarm clock"
              type="time"
              defaultValue="07:30"
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={{
                step: 300, // 5 min
              }}
              //onChange={(e) => console.log(moment(e.target.value, "HH:mm").format('YYYY-MM-DD HH:mm:ss'))}
              onChange={(e) => this.setState({ time: moment(e.target.value, "HH:mm").format('YYYY-MM-DD HH:mm:ss') })}
            />
          </form>
        </div>

        <table style={{width: 100 + '%'}}>
          <tbody>
            <tr>
              <th>Chặng</th>
              <th>Giờ briefing</th>
            </tr>
            <tr>
              <td>Nội địa</td>
              <td><Moment format="HH:mm" subtract={{ hours: 1, minutes: 20 }}>{this.state.time}</Moment></td>
            </tr>
            <tr>
              <td>Ngắn quốc tế</td>
              <td><Moment format="HH:mm" subtract={{ hours: 1, minutes: 35 }}>{this.state.time}</Moment></td>
            </tr>
            <tr>
              <td>Dài quốc tế</td>
              <td><Moment format="HH:mm" subtract={{ hours: 2, minutes: 5 }}>{this.state.time}</Moment></td>
            </tr>
          </tbody>
        </table>

        <p style={{fontSize: 12 + 'px', fontStyle: 'italic'}}>Lưu ý: Nội địa có mặt trước 1h20p, Ngắn quốc tế có mặt trước 1h35p và Dài quốc tế có mặt trước 2h5p</p>
      </Layout>
    </div>
  }
}


export default IndexPage
